.popup__title {
  font-family: 'Inter', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;

  width: 100%;

  margin: 0;
  padding: 0;
}

@media screen and (max-width: 960px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
  }
}