.elements {
  display: grid;
  grid-template-columns: repeat(auto-fill, 282px);
  gap: 20px 16px;
  max-width: calc(1280px - 2 * 200px);
  justify-content: center;

  margin: 50px auto 0;
  padding: 0;
}

@media screen and (max-width: 480px) {
  .elements {
    margin: 36px auto 0;
  }
}